.container {
  --column-count: 1;
  display: grid;
  grid-template-columns: repeat(var(--column-count), 200px);
  padding: 50px 40px 72px;
  column-gap: 48px;
  row-gap: 46px;
  justify-content: center;
}
@media (max-width: 488px) {
  .container {
    $column-count: 1;
  }
}

@media (min-width: 488px) {
  .container {
    --column-count: 2;
  }
}

@media (min-width: 768px) {
  .container {
    --column-count: 3;
  }
}

@media (min-width: 1024px) {
  .container {
    --column-count: 4;
  }
}
